.home{
  /* background-image: url("https://cdn.pixabay.com/photo/2014/03/24/13/49/arabic-294498_960_720.png"); */
  background-image: url("./components/img/homeBg.jpg");
  background-size: cover;
  height: 85vh;
  background-repeat: no-repeat;
  /*clip-path: polygon(0 0, 100% 0, 100% 87%, 0 100%);*/
  position: relative;
  background-position: center;
}
.contact{
  /* background-image: url("https://images.unsplash.com/photo-1576983161955-044b3f32d944?ixlib=rb-1.2.1&auto=format&fit=crop&w=1608&q=80"); */
  background-image: url("https://images.unsplash.com/photo-1525915818695-5b1a033247f1?ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80");
  background-attachment: fixed;
  background-position: center center;
  background-repeat: no-repeat;
}
.bg-dark{
  opacity: 0.6;
  background: #5B3256 !important;
  z-index: 1;
  color: #000;
}
.navbar a{
  color: rgb(172, 175, 177) !important;
}
.nav-item a{
  color: aliceblue !important;
}
.header{
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#footer-section{
  background-image: url("./components/img/footer.jpg");
  height: 55vh;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.footer a{
  border: 3px solid #fff;
  margin: 5px;
  border-radius: 10px 0 10px 0;
}
.footer a:hover{
  transform:rotate(360deg);
  -ms-transform:rotate(360deg);
  -webkit-transform:rotate(360deg);
  transform: translateY(10px);
  background-color: #5B3256;
  transition: all ease .6s;
}
.address{
  position: relative;
  margin-top: -95px;
  color: #fff;
}
.address p{
  margin: 0;
  padding: 0;
}
